import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";

const SearchHome = () => {
  const dispatch = useDispatch();

  const [keyword, setKeyword] = useState("");

  const history = useHistory();

  const handleSubmit = (e) => {
    e.preventDefault();
    if (keyword.trim()) {
      history.push(`/search/${keyword}`);
    } else {
      history.push("/");
    }
  };
  return (
    <header aria-label="Page Header" className="bg-gray-50">
      <div className="mx-auto max-w-screen-xl px-4 py-8 sm:px-6 lg:px-8">
        <div className="mt-8 pb-10">
          <h1 className="text-2xl font-bold text-gray-900 sm:text-3xl">
            All Products
          </h1>

          <p className="mt-1.5 text-sm text-gray-500">
            Search Products, Buy Products, Keep it up! 🚀
          </p>
        </div>

        <div className="flex items-center ">
          <div className="relative hidden sm:block">
            <form onSubmit={handleSubmit}>
              <label className="sr-only" for="search">
                {" "}
                Search{" "}
              </label>

              <input
                className="h-10 w-full rounded-lg border-none bg-white pl-4 pr-10 text-sm shadow-sm sm:w-56"
                id="search"
                onChange={(e) => setKeyword(e.target.value)}
                style={{ width: "400px" }}
                type="search"
                name="q"
                placeholder="Search products..."
              />

              <button
                type="button"
                onClick={handleSubmit}
                className="absolute top-1/2 right-1 -translate-y-1/2 rounded-md bg-gray-50 p-2 text-gray-600 transition hover:text-gray-700"
              >
                <span className="sr-only">Submut Search</span>
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  className="h-4 w-4"
                  fill="none"
                  viewBox="0 0 24 24"
                  stroke="currentColor"
                  stroke-width="2"
                >
                  <path
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
                  />
                </svg>
              </button>
            </form>
          </div>
        </div>
      </div>
    </header>
  );
};

export default SearchHome;
