import React from "react";
import { MdAppSettingsAlt } from "react-icons/md";
import Typewriter from "typewriter-effect";

const Banner = () => {
  return (
    <section className="bg-gray-50">
      <div className="mx-auto max-w-screen-xl px-4 py-32 lg:flex lg:h-96 lg:items-center">
        <div className="mx-auto max-w-xl text-center">
          <h1 className="text-3xl font-extrabold sm:text-5xl">
            Project By Syed Hadi
            <strong className="font-extrabold text-indigo-700 sm:block">
              <Typewriter
                options={{
                  strings: [
                    "Smart Tv",
                    "Cameras",
                    "Mobiles",
                    "Laptops",
                    "Acccessories",
                    "Tripods",
                    "Headphones",
                    "Watches",
                  ],
                  autoStart: true,
                  loop: true,
                }}
              />
            </strong>
          </h1>

          <p className="mt-4 sm:text-xl sm:leading-relaxed">
            Amazing e-commerce platform for everyone
          </p>

          <div className="mt-8 flex flex-wrap justify-center gap-4">
            <a
              className="block w-full rounded bg-indigo-600 px-12 py-3 text-sm font-medium text-white shadow hover:bg-indigo-700 focus:outline-none focus:ring active:bg-indigo-500 sm:w-auto"
              href="https://hadiraza.com/"
              target="#"
            >
              Portfolio
            </a>

            <a
              className="block w-full rounded px-12 py-3 text-sm font-medium text-indigo-600 shadow hover:text-indigo-700 focus:outline-none focus:ring active:text-indigo-500 sm:w-auto"
              href="https://hadiraza.com/#contact"
              target="#"
            >
              Contact Me
            </a>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Banner;
