import React from "react";

// icons
import { FiMonitor, FiWatch } from "react-icons/fi";
import { CiCamera, CiHeadphones, CiLaptop, CiMobile3 } from "react-icons/ci";
import { MdCable } from "react-icons/md";
import { Link } from "react-router-dom";

const Categories = ({cats}) => {
  return (
    <section class="text-gray-600 body-font">
      <div class="container px-5 pt-10 mx-auto">
        <div class="flex flex-col text-center w-full mb-2">
          <div class="flex flex-col md:pr-10 md:mb-0 mb-6 pr-0 w-full md:w-auto md:text-left text-center">
            <h2 class="text-xs text-indigo-500 tracking-widest font-medium title-font mb-1">
              {cats ? "02" : "01"}
            </h2>
            <h1 class="md:text-3xl text-2xl font-medium title-font text-gray-700">
             Categories
            </h1>
          </div>
        </div>
        <div class="flex flex-wrap -m-2">
          <div class="p-2 lg:w-1/3 md:w-1/2 w-full">
            <Link to={`/category/smarttv`}>
              <div class="h-full flex items-center border-gray-200 border p-4 rounded-lg">
                {/* <img alt="team" class="w-16 h-16 bg-gray-100 object-cover object-center flex-shrink-0 rounded-full mr-4" src="https://dummyimage.com/80x80"> */}
                <FiMonitor
                  size={40}
                  color="blue"
                  className="w-16 h-16 bg-gray-100 p-1 object-cover object-center flex-shrink-0  mr-4"
                />
                <div class="flex-grow">
                  <h2 class="text-gray-900 title-font font-medium">Smart TV</h2>
                </div>
              </div>
            </Link>
          </div>
          <div class="p-2 lg:w-1/3 md:w-1/2 w-full">
            <Link to={`/category/mobile`}>
              <div class="h-full flex items-center border-gray-200 border p-4 rounded-lg">
                <CiMobile3
                  size={40}
                  color="blue"
                  className="w-16 h-16 bg-gray-100 p-1 object-cover object-center flex-shrink-0  mr-4"
                />
                <div class="flex-grow">
                  <h2 class="text-gray-900 title-font font-medium">
                    Mobile Phones
                  </h2>
                </div>
              </div>
            </Link>
          </div>

          <div class="p-2 lg:w-1/3 md:w-1/2 w-full">
            <Link to={`/category/cameras`}>
              <div class="h-full flex items-center border-gray-200 border p-4 rounded-lg">
                <CiCamera
                  size={40}
                  color="blue"
                  className="w-16 h-16 bg-gray-100 p-1 object-cover object-center flex-shrink-0  mr-4"
                />
                <div class="flex-grow">
                  <h2 class="text-gray-900 title-font font-medium">Cameras</h2>
                </div>
              </div>
            </Link>
          </div>

          <div class="p-2 lg:w-1/3 md:w-1/2 w-full">
            <Link to={`/category/watch`}>
              <div class="h-full flex items-center border-gray-200 border p-4 rounded-lg">
                <FiWatch
                  size={40}
                  color="blue"
                  className="w-16 h-16 bg-gray-100 p-1 object-cover object-center flex-shrink-0  mr-4"
                />
                <div class="flex-grow">
                  <h2 class="text-gray-900 title-font font-medium">Watches</h2>
                </div>
              </div>
            </Link>
          </div>

          <div class="p-2 lg:w-1/3 md:w-1/2 w-full">
            <Link to={`/category/laptops`}>
              <div class="h-full flex items-center border-gray-200 border p-4 rounded-lg">
                <CiLaptop
                  size={40}
                  color="blue"
                  className="w-16 h-16 bg-gray-100 p-1 object-cover object-center flex-shrink-0  mr-4"
                />
                <div class="flex-grow">
                  <h2 class="text-gray-900 title-font font-medium">Laptops</h2>
                </div>
              </div>
            </Link>
          </div>
          {/* MdCable */}

          <div class="p-2 lg:w-1/3 md:w-1/2 w-full">
            <Link to={`/category/accessories`}>
              <div class="h-full flex items-center border-gray-200 border p-4 rounded-lg">
                <MdCable
                  size={40}
                  color="blue"
                  className="w-16 h-16 bg-gray-100 p-1 object-cover object-center flex-shrink-0  mr-4"
                />
                <div class="flex-grow">
                  <h2 class="text-gray-900 title-font font-medium">
                    {" "}
                    Accessories
                  </h2>
                </div>
              </div>
            </Link>
          </div>

          <div class="p-2 lg:w-1/3 md:w-1/2 w-full">
            <Link to={`/category/headphones`}>
              <div class="h-full flex items-center border-gray-200 border p-4 rounded-lg">
                <CiHeadphones
                  size={40}
                  color="blue"
                  className="w-16 h-16 bg-gray-100 p-1 object-cover object-center flex-shrink-0  mr-4"
                />
                <div class="flex-grow">
                  <h2 class="text-gray-900 title-font font-medium">
                    {" "}
                    Headphones
                  </h2>
                </div>
              </div>
            </Link>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Categories;
